import { Injectable } from '@angular/core';
import { AlertController } from 'ionic-angular';

@Injectable()
export class AlertProvider {

  constructor(public alertCtrl: AlertController) {

  }

  showAlert(message) {
    let alert = this.alertCtrl.create({
      title: '알림',
      subTitle: message,
      buttons: ['OK']
    });
    alert.present();
  }

  showConfirm(message, positiveText, negativeText, positiveHanddler, negativeHandler) {
    let alert = this.alertCtrl.create({
      title: '알림',
      message: message,
      buttons: [
        {
          text: positiveText,
          handler: positiveHanddler
        },
        {
          text: negativeText,
          role: 'cancel',
          handler: negativeHandler
        }
      ]
    });
    alert.present();
  }

  
}



