import { Component } from '@angular/core';
import { NavController, NavParams, ViewController } from 'ionic-angular';
import { LoadingProvider } from '../../providers/loading/loading';
import { HttpProvider } from '../../providers/http/http';
import { AuthProvider } from '../../providers/auth/auth';
import { AlertProvider } from '../../providers/alert/alert';

@Component({
  selector: 'page-question',
  templateUrl: 'question.html',
})
export class QuestionPage {

  constructor(public navCtrl: NavController, public navParams: NavParams,
    public viewCtrl: ViewController,
    public loadingCtrl: LoadingProvider,
    public http: HttpProvider,
    public auth: AuthProvider,
    public alertCtrl: AlertProvider

  ) {


  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad QuestionPage');
  }

  sendQuestion(email, context) {


    if (email.value.indexOf("@") > -1 && email.value.indexOf(".") > -1 && context.value && context.value.trim() != "") {
      this.loadingCtrl.present();
      this.http.sendQuestion({
        email: email.value,
        context: context.value,
        uid: this.auth.user.uid
      }).then(res => {
        this.alertCtrl.showAlert("문의하신 내용이 전달되었습니다. 최대한 빠른 답변드리겠습니다. 감사합니다. : ) ");
        this.loadingCtrl.dismiss();
        this.dismiss();
      })
        .catch(err => {

          this.alertCtrl.showAlert("네트워크 상태가 원활하지 않습니다. 잠시 후 다시 시도해 주세요 :() ");
          this.loadingCtrl.dismiss();

        })
    } else {

      this.alertCtrl.showAlert("양식을 정확히 작성해 주세요 : ) ");
    }
  }

  dismiss() {
    this.viewCtrl.dismiss();
  }
}



