import { Component } from '@angular/core';
import { NavController, NavParams, ViewController, Events } from 'ionic-angular';
import { HttpProvider } from '../../providers/http/http'
import { AuthProvider } from '../../providers/auth/auth'
import { LoadingProvider } from '../../providers/loading/loading'
import { AlertProvider } from '../../providers/alert/alert'

@Component({
  selector: 'page-my-profile',
  templateUrl: 'my-profile.html',
})
export class MyProfilePage {
  selected_type = "my_place"
  nickname;
  address_text;
  my_place = []
  my_place_list = []
  // my_review = []
  // my_review_list = []
  my_bookmark = []
  my_bookmark_list = []

  constructor(public navCtrl: NavController, public navParams: NavParams,
    public viewCtrl: ViewController,
    public http: HttpProvider,
    public auth: AuthProvider,
    public loadingCtrl: LoadingProvider,
    public events: Events,
    public alertCtrl: AlertProvider
  ) {

  }

  ionViewDidLoad() {
    this.nickname = this.auth.user.nickname;

    console.log(this.auth.user)

    // this.events.subscribe("get_my_address", (address) => {
    //   this.address_text = address
    // })
    // this.events.publish("load_my_address")
    
    this.loadingCtrl.present()
    this.auth.currentUser().getIdToken()
      .then((idToken) => {
        console.log(idToken)
        return this.http.getUserInfo({
          idToken: idToken
        })
      }).then((userInfo: any) => {
        this.loadingCtrl.dismiss()
        userInfo.my_place && Object.keys(userInfo.my_place).map((key, i) => {
          userInfo.my_place[key].place_id = key;
          this.my_place.push(userInfo.my_place[key])
          if (this.my_place_list.length < 50) this.my_place_list.push(userInfo.my_place[key])
        })
        // userInfo.my_review && Object.keys(userInfo.my_review).map((key, i) => {
        //   userInfo.my_review[key].review_id = key;
        //   this.my_review.push(userInfo.my_review[key])
        //   if (this.my_review_list.length < 50) this.my_review_list.push(userInfo.my_review[key])
        // })
        userInfo.my_bookmark && Object.keys(userInfo.my_bookmark).map((key, i) => {
          userInfo.my_bookmark[key].bookmark_id = key;
          this.my_bookmark.push(userInfo.my_bookmark[key])
          if (this.my_bookmark_list.length < 50) this.my_bookmark_list.push(userInfo.my_bookmark[key])
        })
      })
      .catch((error) => {
        console.log(JSON.stringify(error))
        this.loadingCtrl.dismiss()
      })

  }

  ionViewWillLeave() {
    this.events.unsubscribe("get_my_address")
  }

  removePlace(place) {
    this.alertCtrl.showConfirm("삭제하시겠습니까?", "네", "아니오", ()=>{

      this.loadingCtrl.present();
      this.auth.currentUser().getIdToken()
      .then((idToken) => {
        var obj = place;
        obj.idToken = idToken;
        return this.http.removePlace(obj)
      }).then((res)=>{
        this.my_place.splice(this.my_place.indexOf(place),1);
        let index = this.my_place_list.indexOf(place)
        if(index > -1 ) this.my_place_list.splice(index,1) 

        this.events.publish("refresh")
        this.loadingCtrl.dismiss();
        this.alertCtrl.showAlert("삭제되었습니다.")
      }).catch(error=>{
        console.log(JSON.stringify(error))
        this.loadingCtrl.dismiss()
      })
    }, ()=>{})
  }

  clickMyBookmark( bookmark, e){
    if(e.target.tagName == "DIV"){
      this.events.publish("view_marker", bookmark)
      this.dismiss()
    }
  }

  removeBookmark(bookmark){
    var obj = bookmark;
    obj.place_id = bookmark.bookmark_id;
    obj.uid = this.auth.user.uid;

    this.http.removeBookmark(bookmark).then(()=>{
      this.my_bookmark.splice(this.my_bookmark.indexOf(bookmark),1);
      let index = this.my_bookmark_list.indexOf(bookmark)
      if(index > -1 ) this.my_bookmark_list.splice(index,1) 

      this.alertCtrl.showAlert("삭제되었습니다.")
    }).catch((error)=>{
      this.alertCtrl.showAlert("네트워크 상태가 원활하지 않습니다.")
      console.log(JSON.stringify(error))
    })
  }   


  doInfinite(infiniteScroll) {
    switch (this.selected_type) {
      case "my_place":
        let list_count = this.my_place_list.length;
        setTimeout(() => {
          for (let i = 0; i < 50; i++) {
            if (this.my_place_list.length < this.my_place.length) {
              this.my_place_list.push(this.my_place[list_count + i]);
            }
          }
          infiniteScroll.complete();
        }, 500);
        break;
      case "my_bookmark":
        let my_bookmark = this.my_bookmark_list.length;
        setTimeout(() => {
          for (let i = 0; i < 50; i++) {
            if (this.my_bookmark_list.length < this.my_bookmark.length) {
              this.my_bookmark_list.push(this.my_bookmark[list_count + i]);
            }
          }
          infiniteScroll.complete();
        }, 500);
        break;

      // case "my_review":
      //   let my_review = this.my_review_list.length;
      //   setTimeout(() => {
      //     for (let i = 0; i < 50; i++) {
      //       if (this.my_review_list.length < this.my_review.length) {
      //         this.my_review_list.push(this.my_review[list_count + i]);
      //       }
      //     }
      //     infiniteScroll.complete();
      //   }, 500);
      //   break;
    }

  }

  dismiss() {
    this.viewCtrl.dismiss();
  }

  clickMyPlace(my_place, e) {
    if(e.target.tagName == "DIV"){
      this.events.publish("view_marker", my_place)
      this.dismiss()
    }
   
  }

}



