import { SQLite } from '@ionic-native/sqlite';
import { Injectable } from '@angular/core';


@Injectable()
export class SqliteProvider {

  constructor(public sqlite: SQLite) {

  }

  Connect() {
    return this.sqlite.create({
      name: 'mentor.db',
      location: 'default'
    })
  }


}



