import { Component } from '@angular/core';
import { NavController, NavParams, ModalController } from 'ionic-angular';
import { MyProfilePage } from '../my-profile/my-profile';
import { CenterPage } from '../center/center';
import { SettingPage } from '../setting/setting';


@Component({
  selector: 'page-popover',
  templateUrl: 'popover.html',
})
export class PopoverPage {

  constructor(public navCtrl: NavController, public navParams: NavParams,
    public modalCtrl: ModalController
  ) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PopoverPage');
  }

  selectItem(menu_name){
    if(menu_name == '내 정보'){
      const modal = this.modalCtrl.create(MyProfilePage);
      modal.present();
      // this.navCtrl.push(MyProfilePage)
    }
    else if(menu_name == '금연치료의료기관'){
      const modal = this.modalCtrl.create(CenterPage);
      modal.present();
      // this.navCtrl.push(CenterPage)
    }else if(menu_name == '환경설정'){
      const modal = this.modalCtrl.create(SettingPage);
      modal.present();
      // this.navCtrl.push(SettingPage)
    }
  }

}



