import { Component } from '@angular/core';
import { NavController, NavParams, Events, ViewController } from 'ionic-angular';
import { HttpProvider } from '../../providers/http/http';
import { LoadingProvider } from '../../providers/loading/loading';


@Component({
  selector: 'page-center',
  templateUrl: 'center.html',
})
export class CenterPage {
  list = [];
  data = [];

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public http: HttpProvider,
    public events: Events,
    public loadingCtrl: LoadingProvider,
    public viewCtrl: ViewController
  ) {

  }

  ionViewDidLoad() {
    this.loadingCtrl.present();
    this.http.loadData(false)
      .then((data) => {
        this.loadingCtrl.dismiss();
        data['center'].forEach(v => {
          var obj = v.toJSON()
          obj.place_id = v.key
          this.data.push(obj)
          if (this.list.length < 50) this.list.push(obj)
        });
      }).catch(error => {
        this.loadingCtrl.dismiss();
        console.log(error)
      })
  }

  doInfinite(infiniteScroll) {
    var list_count = this.list.length;
    setTimeout(() => {
      for (let i = 0; i < 50; i++) {
        if (this.list.length < this.data.length) {
          this.list.push(this.data[list_count + i]);
        }
      }

      infiniteScroll.complete();
    }, 500);
  }

  searchItems(e) {
    var item = e.target.value;

    if (item == "") {
      this.list = [];
      this.data.map((v, i) => {
        if (this.list.length < 50) this.list.push(v)
      });
    } else {
      this.list = [];
      this.data.map((v, i) => {
        if (v.name.indexOf(item) > -1 || v.address1.indexOf(item) > -1) {
          this.list.push(v)
        }
      });
    }

  }

  viewCenterDetail(center) {
    this.events.publish("view_marker", center)
    this.navCtrl.pop();
  }

  dismiss(){
    this.viewCtrl.dismiss();
  }

}






