import { Component } from '@angular/core';
import { NavController, NavParams, Events, ViewController} from 'ionic-angular';
import { HttpProvider } from '../../providers/http/http'
import { AlertProvider } from '../../providers/alert/alert'
import { AuthProvider } from '../../providers/auth/auth'
import { LoadingProvider } from '../../providers/loading/loading'
import { MapSearchPage } from '../map-search/map-search'

@Component({
  selector: 'page-place-modify',
  templateUrl: 'place-modify.html',
})
export class PlaceModifyPage {

  address1;
  address2;
  category;
  smoking_place_type;
  no_smoking_place_type;
  name;
  description1;
  description2;
  description3;
  operator;
  latitude;
  longitude;
  place_id;

  constructor(public navCtrl: NavController, public navParams: NavParams,
    public http: HttpProvider,
    public alertCtrl: AlertProvider,
    public viewCtrl : ViewController,
    public events: Events,
    // public AlertCtrl: AlertController,
    public auth: AuthProvider,
    public loadingCtrl: LoadingProvider
  
  ) {
    this.address1 = navParams.get("address1") || ""
    this.address2 = navParams.get("address2") || ""
    this.category = navParams.get("category") || ""
    this.smoking_place_type = this.category == "흡연 구역"? navParams.get("type") : ""
    this.no_smoking_place_type = this.category == "금연 구역"? navParams.get("type") : ""
    this.name = navParams.get("name") || ""
    this.description1 = navParams.get("description1") || ""
    this.description2 = navParams.get("description2") || ""
    this.description3 = navParams.get("description3") || ""
    this.operator = navParams.get("operator") || ""
    this.latitude = navParams.get("latitude") || ""
    this.longitude = navParams.get("longitude") || ""
    this.place_id = navParams.get("place_id") || ""

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PlaceModifyPage');
  }

  dismiss(){
    this.viewCtrl.dismiss();
  }

  clickModify(){

    if(this.name == ""){
      this.alertCtrl.showAlert("장소명을 입력하세요.")
    }

    this.alertCtrl.showConfirm("수정하시겠습니까? (정확한 장소가 아닐 경우 삭제될 수 있습니다.)", "네", "아니오", () => {
      if (this.auth.user) {
        this.loadingCtrl.present()
        this.auth.currentUser().getIdToken()
          .then((idToken) => {
            return this.http.modifyPlace({
              place_id : this.place_id,
              address1: this.address1,
              address2: this.address2,
              category: this.category,
              description1: this.description1 == ""? null :  this.description1,
              description2: this.description2 == ""? null :  this.description2,
              description3: this.description3 == ""? null :  this.description3,
              editor: this.auth.user.nickname,
              password: this.auth.user.uid,
              // latitude: this.latitude,
              // longitude: this.longitude,
              name: this.name,
              operator: this.operator,
              type: this.category == "흡연 구역" ? this.smoking_place_type : this.no_smoking_place_type,
              // idToken: idToken
            })

          }).then((res: any) => {
            this.events.publish("refresh")
            this.loadingCtrl.dismiss()
            this.alertCtrl.showAlert("정보가 수정 되었습니다.")
            this.dismiss()
          }).catch(error => {
            this.loadingCtrl.dismiss()
            console.log(JSON.stringify(error))
            this.alertCtrl.showAlert("네트워크 상태가 원활하지 않습니다.")
          })
      } 
    }, () => { })



  }

}



