import { Component, ViewChild, ElementRef } from '@angular/core';
import { NavController, NavParams, ViewController, Platform } from 'ionic-angular';
import {
  LocationService,
  GoogleMaps,
  GoogleMap,
  GoogleMapsEvent,
  GoogleMapOptions,
  CameraPosition,
  MarkerOptions,
  Marker,
  MyLocation
} from '@ionic-native/google-maps';
import { Geolocation } from '@ionic-native/geolocation';

import { LoadingProvider } from '../../providers/loading/loading';

@Component({
  selector: 'page-map-search',
  templateUrl: 'map-search.html',
})
export class MapSearchPage {
  @ViewChild('map') mapElement: ElementRef;
  map: GoogleMap;
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public loadingCtrl: LoadingProvider,
    public geolocation: Geolocation,
    public viewCtrl: ViewController,
    private platform: Platform
  ) {
    platform.ready().then(()=>{
      this.mapInit()
      .then(() => {
        alert("Ok")
        this.moveToMyLocation()
      }).catch((error) => {
        console.log(error)
        alert(JSON.stringify(error))
      })
    })
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MapSearchPage');
    // this.mapInit()
    //   .then(() => {
    //     this.moveToMyLocation()
    //   }).catch((error) => {
    //     console.log(error)
    //   })
  }

  ngAfterViewInit() {
    console.log("init")
    // this.mapInit()
    //   .then(() => {
    //     alert("Ok")
    //     this.moveToMyLocation()
    //   }).catch((error) => {
    //     console.log(error)
    //     alert(JSON.stringify(error))
    //   })
  }
  mapInit() {
    let options: GoogleMapOptions = {
      controls: {
        // compass: true,
        myLocation: true,
        myLocationButton: true,
        indoorPicker: false,
        mapToolbar: false
      },
      camera: {
        target: {
          lat: 37.5662994,
          lng: 126.9757564
        },
        zoom: 18
      }
    };

    this.map = GoogleMaps.create('map_canvas2', options)
    return this.map.one(GoogleMapsEvent.MAP_READY)

  }

  mapEventBinding() {
    this.map.on(GoogleMapsEvent.MAP_CLICK).subscribe((params) => {
      let latLng = params[0];
      let map: GoogleMap = params[1];  // <-- You can get the target of MAP_CLICK event


    })
  }


  moveToMyLocation() {
    this.getMyLocation().then((myLocation: MyLocation) => {

      this.map && this.map.moveCamera({
        target: myLocation.latLng,
        zoom: 18
      });
    }).catch(error => {
      console.log(JSON.stringify(error))
    })

  }

  getMyLocation() {
    return LocationService.getMyLocation();
  }



  dismiss() {
    this.viewCtrl.dismiss();
  }

}



