import { Component, ViewChild, NgZone  } from '@angular/core';
import { Nav, Platform, Events, Content} from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
// import { AngularFireAuth } from 'angularfire2/auth';
// import { AngularFireDatabase } from 'angularfire2/database';

import { MapPage } from '../pages/map/map';
// import { IntroPage } from '../pages/intro/intro';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;
  @ViewChild(Content) content: Content;

  rootPage: any = MapPage;


  chat = [];
  latitude;

  constructor(public platform: Platform,
    public statusBar: StatusBar,
    public splashScreen: SplashScreen,
    public events: Events,
    
    public zone: NgZone
    // public FirebaseAuth: AngularFireAuth,
    // public FirebaseDB: AngularFireDatabase
  ) {
    this.initializeApp();
    // this.chat = [{
    //   uid : '1',
    //   content : "111",
    //   distance : "1.1",
    //   date : "3:43 pm",
    //   nickname : "#!!!!"
    // }];

  }

  initializeApp() {
    this.platform.ready().then(() => {

      // this.statusBar.styleDefault();
      this.statusBar.backgroundColorByHexString("#2196F3")
      this.splashScreen.hide();

      this.events.subscribe("add_chat_message", (data: any) => {

        this.chat.push({
          uid: data.uid,
          content: data.content,
          distance : data.distance,
          date : data.date,
          nickname : data.nickname
        })

        this.zone.run(() => {
          this.content.resize();
        })
      })

      this.events.subscribe("get_my_location", (data: any) => {
        this.latitude = parseFloat(data.lat).toFixed(5) || ""
      })

    });

  }

  // openPage(page) {    
  //   this.nav.setRoot(page.component);   
  // }
}



