
import { Injectable } from '@angular/core';
import { AngularFireAuth } from 'angularfire2/auth';
import { SQLiteObject } from '@ionic-native/sqlite';
import { SqliteProvider } from '../sqlite/sqlite';
import { HttpProvider } from '../http/http';

@Injectable()
export class AuthProvider {

  user;

  constructor(public FirebaseAuth: AngularFireAuth,
    public sqlite: SqliteProvider,
    public http: HttpProvider
  ) {
    console.log('Hello AuthProvider Provider');

  }

  getUser() {
    let _db;
    return new Promise((resolve, reject) => {
      if (this.user) {

        resolve(this.user)
      } else {
       
        this.sqlite.Connect()
          .then(db => {
            _db = db;
            return this.createTable(db)           
          }).then(()=>{
            return this.find(_db)
          }).then(result => {
            if (result.rows.length == 0) {

              resolve(null)
            } else {

              resolve(result.rows.item(0))
            }
          }).catch(error => {
            console.log(JSON.stringify(error));
            reject(error)
          })
      }
    })
  }

  createUser() {
    return this.FirebaseAuth.auth.signInAnonymously()
      .then((userRecord: any) => {
        console.log(userRecord.user.uid)
        return userRecord.user
      }).catch(error => {
        console.log(JSON.stringify(error));
        return null;
      })
  }

  signInWithCustomToken(params) {
    let nickname, fcm;
    return this.http.signIn({
      uid: params.uid
    }).then((res: any) => {

      nickname = res.nickname;
      fcm = res.fcm;
      return this.FirebaseAuth.auth.signInWithCustomToken(res.customToken)
      
    }).then((userInfo:any) => {

      this.user = Object.assign({}, {
        uid : userInfo.user.uid,
        nickname : nickname,
        fcm : fcm
      } )
      return this.user
    })
  }

  currentUser(){
    return this.FirebaseAuth.auth.currentUser
  }

  createTable(db: SQLiteObject) {
    var sql = 'CREATE TABLE IF NOT EXISTS user (uid VARCHAR(50) PRIMARY KEY, nickname VARCHAR(50) ,subscribe BOOLEAN NOT NULL)';
    return db.executeSql(sql, [])
  }

  dropTable(db: SQLiteObject) {
    var sql = 'DROP TABLE user';
    return db.executeSql(sql, [])
  }

  find(db: SQLiteObject) {
    var sql = 'SELECT * FROM user';
    return db.executeSql(sql, []);
  }

  insert(db: SQLiteObject, uid: string, nickname: string, subscribe: boolean) {
    var sql = 'INSERT INTO user VALUES("' + uid + '", "' + nickname + '" , "' + subscribe + '")';
    return db.executeSql(sql, []);
  }

  update(db: SQLiteObject, id: string, subscribe: boolean) {
    var sql = "UPDATE user SET id = '" + subscribe + "' WHERE id = '" + id + "' "
    return db.executeSql(sql, []);
  }

}



