import { Component } from '@angular/core';
import { NavController, NavParams, Events, ViewController } from 'ionic-angular';
import { HttpProvider } from '../../providers/http/http';
import { LoadingProvider } from '../../providers/loading/loading';

@Component({
  selector: 'page-search-place',
  templateUrl: 'search-place.html',
})
export class SearchPlacePage {
  smoking_list = [];
  smoking_data = [];
  no_smoking_list = [];
  no_smoking_data = [];
  selected_place_type = "smoking"

  constructor(public navCtrl: NavController, public navParams: NavParams,
    public http: HttpProvider,
    public loadingCtrl : LoadingProvider,
    public events: Events,
    public viewCtrl: ViewController
  ) {

  }

  ionViewDidLoad() {
    this.loadingCtrl.present();
    console.log('ionViewDidLoad SarchPlacePage');
    this.http.loadData(false).then((data) => {
      this.loadingCtrl.dismiss();
      data['smoking'].forEach(v => {
        var obj = v.toJSON()
        obj.place_id = v.key
        this.smoking_data.push(obj)
        if (this.smoking_list.length < 50) this.smoking_list.push(obj)
      });
        data['no_smoking'].forEach(v => {
        var obj = v.toJSON();
        obj.place_id = v.key;
        this.no_smoking_data.push(obj)
        if (this.no_smoking_list.length < 50) this.no_smoking_list.push(obj)
      });
    })

  }

  doInfinite(infiniteScroll) {

    setTimeout(() => {
      if (this.selected_place_type == "smoking") {
        let smoking_list_count = this.smoking_list.length;
        for (let i = 0; i < 50; i++) {
          if (this.smoking_list.length < this.smoking_data.length) {
            this.smoking_list.push(this.smoking_data[smoking_list_count + i]);
          }
        }
      } else {
        let no_smoking_list_count = this.no_smoking_list.length;
        for (let i = 0; i < 50; i++) {
          if (this.no_smoking_list.length < this.no_smoking_data.length) {
            this.no_smoking_list.push(this.no_smoking_data[no_smoking_list_count + i]);
          }
        }
      }

      infiniteScroll.complete();
    }, 500);
  }


  searchItems(e) {
    var item = e.target.value;

    if (item == "") {
      if (this.selected_place_type == "smoking") {
        this.smoking_list = [];
        this.smoking_data.map((v, i) => {
          if (this.smoking_list.length < 50) this.smoking_list.push(v)
        });
      } else {
        this.no_smoking_list = [];
        this.no_smoking_data.map((v, i) => {
          if (this.no_smoking_list.length < 50) this.no_smoking_list.push(v)
        });
      }

    } else {
      if (this.selected_place_type == "smoking") {
        this.smoking_list = [];
        this.smoking_data.map((v, i) => {
          if (v.name.indexOf(item) > -1 || v.address1.indexOf(item) > -1|| v.address2.indexOf(item) > -1) {
            this.smoking_list.push(v)
          }
        });
      } else {
        this.no_smoking_list = [];
        this.no_smoking_data.map((v, i) => {
          if (v.name.indexOf(item) > -1 || v.address1.indexOf(item) >-1|| v.address2.indexOf(item)>-1) {
            this.no_smoking_list.push(v)
          }
        });
      }
    }
  }

  viewPlaceDetail(place) {
    console.log(place)
    this.events.publish("view_marker", place)
    this.navCtrl.pop();
  }

  dismiss(){
    this.viewCtrl.dismiss();
  }
}



