import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';
import { AlertProvider } from '../alert/alert';



@Injectable()
export class HttpProvider {

  data;
  API_KEY = `AIzaSyCWsq1qkJbuGKH1xT1oWkmIvyq5-uF44RA`
  BASE_URL = "https://gongzone-4fb9f.firebaseapp.com";

  constructor(public http: HttpClient,
    public alertCtrl: AlertProvider,
    public FirebaseDB: AngularFireDatabase) {
    console.log('Hello HttpProvider Provider');

  }

  signIn(params) {
    return new Promise((resolve, reject) => {
      this.http.post(this.BASE_URL + "/signIn", { uid: params.uid }).subscribe((res: any) => {

        if (res.success == 1) {
          resolve(res)
        } else {
          reject(res.error)
        }
      })
    })

  }

  loadData(refreshForce) {

    if (refreshForce) {
      return new Promise((resolve, reject) => {
        this.FirebaseDB.database.ref("/map").once("value", (datas: any) => {
          this.data = Object.assign({}, {
            smoking: datas.child("smoking"),
            no_smoking: datas.child("no_smoking"),
            center: datas.child("center"),
            warning : datas.child("warning")
          })
          resolve(this.data)
        }, (error) => {
          reject(error);
        })
      })
    } else {
      return new Promise((resolve, reject) => {
        if (this.data) {
          resolve(this.data)
        }
        else {
          this.FirebaseDB.database.ref("/map").once("value", (datas: any) => {
            this.data = Object.assign({}, {
              smoking: datas.child("smoking"),
              no_smoking: datas.child("no_smoking"),
              center: datas.child("center"),
              warning : datas.child("warning")
            })
            resolve(this.data)
          }, (error) => {
            reject(error);
          })
        }
      })
    }


  }

  sendQuestion(params){
    return new Promise((resolve, reject)=>{
      this.FirebaseDB.database.ref("/question").push().set(params, (error)=>{
        if(error){
          reject(error)
        }else{
          resolve(true)
        }
      })
    })
  }

  updateMarkerInfo(params) {
    let type = params.type;
    let latLng = params.latLng;
    let id = params.id;
    this.FirebaseDB.database.ref(`/map/${type}/${id}/latitude`).set(latLng.lat, (error) => {
      if (error) {
        this.alertCtrl.showAlert("네트워크 상태가 원활하지 않습니다.")
        console.log(JSON.stringify(error));
        return;
      }

      this.FirebaseDB.database.ref(`/map/${type}/${id}/longitude`).set(latLng.lng, (error) => {
        if (error) {
          this.alertCtrl.showAlert("네트워크 상태가 원활하지 않습니다.");
          console.log(JSON.stringify(error));
          return;
        }
        this.alertCtrl.showAlert("수정되었습니다!");
      })
    })
  }

  searchGeoLocation(address) {
    var URI = `https://maps.googleapis.com/maps/api/geocode/json?address=${address}&language=ko&key=${this.API_KEY}`
    return new Promise((resolve, reject) => {
      this.http.get(encodeURI(URI), {}).subscribe((res: any) => {
        console.log(res)
        if (res.status == "OK") {
          resolve(res)
        } else {
          reject(res)
        }
      })
    })
  }

  searchAddress(latLng) {
    var url = `https://maps.googleapis.com/maps/api/geocode/json?latlng=${latLng.lat},${latLng.lng}&language=ko&key=${this.API_KEY}`
    return new Promise((resolve, reject) => {
      this.http.get(encodeURI(url), {}).subscribe((res: any) => {

        if (res.status == "OK") {
          resolve(res)
        } else {
          reject(res)
        }
      })
    })
  }

  addPlace(params) {
    var url = `${this.BASE_URL}/addPlace`
    return new Promise((resolve, reject) => {
      this.http.post(url, params).subscribe((res: any) => {
        console.log(res)
        if (res.success == 1) {
          resolve(res)
        } else {
          reject(res)
        }
      })
    })
  }

  addWarningPlace(params){
    return new Promise((resolve, reject) => {
      this.FirebaseDB.database.ref("/map/warning").push().set(params, (error)=>{
        if(error){
          reject(error)
        }else{  
          resolve(true)
        }
      })
    })
  }

  modifyPlace(params) {
    return new Promise((resolve, reject) => {
      let category;
      if (params.category == "흡연 구역") {
        category = "smoking"
      } else if (params.category == "금연 구역") {
        category = "no_smoking"
      } else {
        category = "center"
      }
      this.FirebaseDB.database.ref("/map").child(category).child(params.place_id).child("address2").set(params.address2)
      this.FirebaseDB.database.ref("/map").child(category).child(params.place_id).child("description1").set(params.description1)
      this.FirebaseDB.database.ref("/map").child(category).child(params.place_id).child("description2").set(params.description2)
      this.FirebaseDB.database.ref("/map").child(category).child(params.place_id).child("description3").set(params.description3)
      this.FirebaseDB.database.ref("/map").child(category).child(params.place_id).child("name").set(params.name)
      this.FirebaseDB.database.ref("/map").child(category).child(params.place_id).child("operator").set(params.operator)
      this.FirebaseDB.database.ref("/map").child(category).child(params.place_id).child("date").set(new Date().toString(), (error) => {
        if (error) {
          reject(error)
        } else {
          resolve(true);
        }
      })


    })
  }

  removePlace(params) {
    var url = `${this.BASE_URL}/removePlace`
    return new Promise((resolve, reject) => {
      this.http.post(url, params).subscribe((res: any) => {
        console.log(res)
        if (res.success == 1) {
          resolve(res)
        } else {
          reject(res)
        }
      })
    })
  }

  saveReview(params) {
    var place_id = params.place_id;
    var category = params.category == "흡연 구역" ? "smoking" : (params.category == "금연 구역" ? "no_smoking" : "center")
    var review_content = params.review_content;

    return new Promise((resolve, reject) => {
      this.FirebaseDB.database.ref(`/map/${category}/${place_id}/review`).once("value", (datas)=>{
        let index = datas.numChildren()

        // this.FirebaseDB.database.ref(`/member/${params.uid}/my_review/${index}`).set
        this.FirebaseDB.database.ref(`/map/${category}/${place_id}/review/${index}`).set({
          review_content : review_content,
          nickname : params.nickname,
          uid : params.uid,
          like_count : 0,
          dislike_count : 0,
          date : new Date().toString()
        }, (error)=>{
          if(error){
            reject(error)
          }else{
            if(index == 0){
              resolve({
                review_content : review_content,
                nickname : params.nickname,
                uid : params.uid,
                like_count : 0,
                dislike_count : 0,
                date : new Date().toString()
              })
            }else{              
              let review = [];
              Object.keys(datas.toJSON()).map((v,i)=>{
                review.push(datas.toJSON()[v])
              })

              review.push({
                review_content : review_content,
                nickname : params.nickname,
                uid : params.uid,
                like_count : 0,
                dislike_count : 0,
                date : new Date().toString()
              })

              resolve(review);
            }                  
          }
        })
      })

    })
  }

  getUserInfo(params) {
    var url = `${this.BASE_URL}/getUserInfo`
    return new Promise((resolve, reject) => {
      this.http.post(url, params).subscribe((res: any) => {
        console.log(res)
        if (res.success == 1) {
          resolve(res)
        } else {
          reject(res)
        }
      })
    })
  }

  addBookmark(params){
    var place_id = params.place_id;
    var uid = params.uid;

    // delete params.place_id
    // delete params.uid;

    return new Promise((resolve, reject)=>{
      this.FirebaseDB.database.ref("/member").child(uid).child("my_bookmark").child(place_id).set({
        address1 : params.address1,
        address2 : params.address2,
        category : params.category,
        editor : params.editor,
        hidden : params.hidden,
        latitude : params.latitude,
        longitude : params.longitude,
        name : params.name,
        operator : params.operator,
        password : params.password,
        type : params.type
      }, (error)=>{
        if(error){
          reject(error)
        }else{
          resolve(true);
        }
      })
     
    })
  }

  removeBookmark(params){
    var place_id = params.place_id;
    var uid = params.uid;

    // delete params.place_id;
    // delete params.uid;
    return new Promise((resolve, reject)=>{
      
      this.FirebaseDB.database.ref("/member").child(uid).child("my_bookmark").child(place_id).set(null, (error)=>{
        if(error){
          reject(error)
        }else{
          resolve(true);
        }
      });
    })
  }

  loadPlaceInfo(params) {
    var place_id = params.place_id;
    var category = params.category == "흡연 구역" ? "smoking" : (params.category == "금연 구역" ? "no_smoking" : "center")

    return new Promise((resolve, reject) => {
      this.FirebaseDB.database.ref("/map").child(category).child(params.place_id).once("value", (datas) => {
        if (datas.numChildren() == 0) {
          reject(null)
        } else {
          resolve(datas.toJSON())
        }
      })
    })

  }

  updatePlaceAccuracy(params){
    var place_id = params.place_id;
    var uid = params.uid;
    var nickname = params.nickname;
    var accuracy = params.accuracy;
    var category = params.category == "흡연 구역" ? "smoking" : (params.category == "금연 구역" ? "no_smoking" : "center")
    return new Promise((resolve, reject) => {
      this.FirebaseDB.database.ref("/map").child(category).child(params.place_id).once("value", (datas) => {
        if (datas.numChildren() == 0) {
          reject(null)
        } else {
        
          let accuracy_reviewer =  datas.child("accuracy_reviewer").toJSON();
          let accuracy_reviewer_count = Object.keys(accuracy_reviewer).length;
          let positive_accuracy_reviewer = Object.keys(accuracy_reviewer).filter((v, i)=>{
            if(accuracy_reviewer[v].accuracy == true){
                return v
            }
          })

          let accuracy_rate = parseInt(""+((positive_accuracy_reviewer.length / accuracy_reviewer_count) * 100))
          this.FirebaseDB.database.ref(`/map/${category}/${place_id}/accuracy_rate`).set(accuracy_rate);
          this.FirebaseDB.database.ref(`/map/${category}/${place_id}/accuracy_reviewer/${Object.keys(accuracy_reviewer).length}`).set({
            uid : uid,
            nickname : nickname,
            accuracy : accuracy,
            date : new Date().toString()
          }, (error)=>{
            
            if(error) reject(error)
            else resolve(accuracy_rate)
          })
         
        }
      })
    })

  }

  likeClick(params){
    var place_id = params.place_id;
    var uid = params.uid;
    var category = params.category == "흡연 구역" ? "smoking" : (params.category == "금연 구역" ? "no_smoking" : "center")
    var review_id = params.review_id;
    return new Promise((resolve, reject)=>{
      this.FirebaseDB.database.ref(`/map/${category}/${place_id}/review/${review_id}`).once("value", datas=>{
        var count;
        if(datas.child("like_user").child(uid).exists()){
           count = parseInt(datas.child("like_count").val())
        }else{
           count = parseInt(datas.child("like_count").val()) + 1
           this.FirebaseDB.database.ref(`/map/${category}/${place_id}/review/${review_id}/like_user/${uid}`).set(uid)
           this.FirebaseDB.database.ref(`/map/${category}/${place_id}/review/${review_id}/like_count`).set(count, (error)=>{
            if(error) reject(error)
            else resolve(count)
          })
        }
      
       

      })
    })
  }

  dislikeClick(params){
    var place_id = params.place_id;
    var uid = params.uid;
    var category = params.category == "흡연 구역" ? "smoking" : (params.category == "금연 구역" ? "no_smoking" : "center")
    var review_id = params.review_id;
    return new Promise((resolve, reject)=>{
      this.FirebaseDB.database.ref(`/map/${category}/${place_id}/review/${review_id}`).once("value", datas=>{
        var count;
        if(datas.child("dislike_user").child(uid).exists()){
           count = parseInt(datas.child("dislike_count").val())
        }else{
           count = parseInt(datas.child("dislike_count").val()) + 1
           this.FirebaseDB.database.ref(`/map/${category}/${place_id}/review/${review_id}/dislike_user/${uid}`).set(uid)
           this.FirebaseDB.database.ref(`/map/${category}/${place_id}/review/${review_id}/dislike_count`).set(count, (error)=>{
            if(error) reject(error)
            else resolve(count)
          })
        }   

      })
    })
  }

  sendChat(params){
    var uid = params.uid;
    var nickname = params.nickname
    var content = params.content;
    var latitude = params.latitude;
    var longitude = params.longitude;
    var date = params.date

    this.FirebaseDB.database.ref("/chat").child(new Date().getTime()+uid).set({
      uid : uid,
      nickname : nickname,
      content : content,
      latitude : latitude,
      longitude : longitude,
      date : date
    })

  }

}



