import { Component, ViewChild } from '@angular/core';
import { NavController, NavParams, ViewController, ToastController, Slides, AlertController, Events } from 'ionic-angular';
import { HttpProvider } from '../../providers/http/http'
import { AlertProvider } from '../../providers/alert/alert'
import { AuthProvider } from '../../providers/auth/auth'
import { LoadingProvider } from '../../providers/loading/loading'
import * as moment from 'moment';

@Component({
  selector: 'page-place-review',
  templateUrl: 'place-review.html',
})
export class PlaceReviewPage {
  @ViewChild(Slides) slidesElement: Slides;
  bookmarkAdded = false;
  review = []
  review_content = ""
  place_id = ""
  category
  accuracy_rate;
  address1;

  constructor(public navCtrl: NavController, public navParams: NavParams,
    public viewCtrl: ViewController,
    public toastCtrl: ToastController,
    public AlertCtrl: AlertController,
    public alertCtrl: AlertProvider,
    public http: HttpProvider,
    public auth: AuthProvider,
    public loadingCtrl: LoadingProvider,
    public events: Events
  ) {
    this.place_id = this.navParams.get("place_id");
    this.category = this.navParams.get("category");
    this.address1 = this.navParams.get("address1");
    this.http.loadPlaceInfo({
      place_id: this.place_id,
      category: this.category
    }).then((placeInfo: any) => {

      Object.keys(placeInfo.accuracy_reviewer).map((v, i) => {
        if (placeInfo.accuracy_reviewer[v].uid == this.auth.user.uid) {
          this.accuracy_rate = placeInfo.accuracy_rate
          this.slidesElement.lockSwipes(false);
          this.slidesElement.slideNext()
          this.slidesElement.lockSwipes(true);
        }
      })

      Object.keys(placeInfo.review).map((v, i) => {
        this.review.push(placeInfo.review[v])
      })

      // this.review.push({
      //   review_id : "123",
      //   nickname : "#1212332",
      //   review_content : "여기 너무 지저분해요ㅠㅠㅠㅠㅠㅠㅠㅠㅠㅠㅠㅠㅠㅠㅠㅠㅠㅠㅠㅠㅠㅠㅠㅠㅠㅠㅠㅠㅠ",
      //   like_count : "200",
      //   dislike_count : "300",
      //   date : new Date().toString()
      // }) 
    })

    this.auth.currentUser().getIdToken()
      .then((idToken) => {
        console.log(idToken)
        return this.http.getUserInfo({
          idToken: idToken
        })
      }).then((userInfo: any) => {
        userInfo.my_bookmark && Object.keys(userInfo.my_bookmark).map((key, i) => {
          if (key == this.place_id) {
            this.bookmarkAdded = true;
          }
        })
      })
      .catch((error) => {
        console.log(JSON.stringify(error))
      })
  }

  ionViewDidLoad() {
    this.slidesElement.lockSwipes(true);  

    
  }

  dismiss() {
    this.viewCtrl.dismiss();
  }

  addBookmark() {
    var obj = this.navParams.data;
    obj.uid = this.auth.user.uid;
    if (this.bookmarkAdded) {

      this.http.removeBookmark(obj).then(() => {
        const toast = this.toastCtrl.create({
          message: '북마크존에서 삭제',
          duration: 2000
        });
        toast.present();
        this.bookmarkAdded = !this.bookmarkAdded

      }).catch((error) => {
        this.alertCtrl.showAlert("네트워크 상태가 원활하지 않습니다.")
        console.log(JSON.stringify(error))
      })

    } else {
      this.http.addBookmark(obj).then(() => {
        const toast = this.toastCtrl.create({
          message: '북마크존에 추가',
          duration: 2000
        });
        toast.present();
        this.bookmarkAdded = !this.bookmarkAdded

      }).catch((error) => {
        this.alertCtrl.showAlert("네트워크 상태가 원활하지 않습니다.")
        console.log(JSON.stringify(error))
      })
    }
  }
  likeClick(review){
    this.http.likeClick({
      place_id : this.place_id,
      uid : this.auth.user.uid,
      review_id : this.review.indexOf(review),
      category : this.category
    }).then(count=>{
      this.review[this.review.indexOf(review)].like_count = count

    }).catch(error=>{
      console.log(JSON.stringify(error))
    })
  }

  dislikeClick(review){
    this.http.dislikeClick({
      place_id : this.place_id,
      uid : this.auth.user.uid,
      review_id : this.review.indexOf(review),
      category : this.category
    }).then(count=>{
      this.review[this.review.indexOf(review)].dislike_count = count

    }).catch(error=>{
      console.log(JSON.stringify(error))
    })
  }

  clickAssessment(accuracy) {
    var obj = this.navParams.data;
    obj.uid = this.auth.user.uid;
    obj.nickname = this.auth.user.nickname;
    obj.accuracy = accuracy;

    const toast = this.toastCtrl.create({
      message: '반영되었습니다.',
      duration: 3000
    });
    toast.present();

    this.slidesElement.lockSwipes(false)
    this.slidesElement.slideTo(1)
    this.slidesElement.lockSwipes(true)

    this.http.updatePlaceAccuracy(obj)
      .then((accuracy_rate) => {
        this.accuracy_rate = accuracy_rate
      }).catch((error) => {
        console.log(error)
      })
  }

  converTime(date) {
    date = new Date(date);
    if (moment(date).format('YYYYMMDD') == moment().format('YYYYMMDD')) {
      return moment(date).format('hh:mm a')
    } else {
      return moment(date).format('YYYY-MM-DD')
    }
  }

  sendReview() {
    console.log(this.review_content)
    let alert = this.AlertCtrl.create({
      // title: '리뷰 작성',
      subTitle: "리뷰 작성",
      inputs: [
        {
          name: 'review_content',
          placeholder: '리뷰를 입력하세요.'
        }
      ],
      buttons: [
        {
          text: '취소',
          role: 'cancel',
          handler: data => {
            console.log('Cancel clicked');
          }
        },
        {
          text: '완료',
          handler: data => {
            
            this.review_content = data.review_content
            
            var obj = this.navParams.data;
            obj.uid = this.auth.user.uid;
            obj.nickname = this.auth.user.nickname;
            obj.review_content = this.review_content;

            if (this.auth.user) {
              this.loadingCtrl.present()
              this.auth.currentUser().getIdToken()
                .then((idToken) => {
                  return this.http.saveReview(obj)

                }).then((review: any) => {
                  this.review = review;
                  this.loadingCtrl.dismiss()
                  this.alertCtrl.showAlert("리뷰가 작성 되었습니다.")
                  this.dismiss()
                }).catch(error => {
                  this.loadingCtrl.dismiss()
                  console.log(JSON.stringify(error))
                  this.alertCtrl.showAlert("네트워크 상태가 원활하지 않습니다.")
                })
            }
          }
        }
      ]
    });
    alert.present();
  }

}



